.checkbox,
.radio {
  padding-left: 0px;
  min-width: 27px; }

input[type='checkbox'],
input[type='radio'] {
  display: none; }

.checkbox > label::before,
.radio::before,
.radio-inline::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 1px solid #888;
  border-radius: 2px;
  background: radial-gradient(white, #eee);
  margin-right: 5px; }

.radio::before {
  border-radius: 50%; }

.indeterminate-checkbox input[type='checkbox'] + label::before,
.indeterminate-checkbox input[type='checkbox'] + span::before {
  content: '–';
  color: #c94281;
  font-size: 14px;
  margin-left: -17px;
  top: 1px;
  position: relative;
  margin-right: 8px;
  font-weight: bolder; }

input[type='checkbox']:checked + label::before,
input[type='checkbox']:checked + span::before {
  content: '✓';
  color: #c94281;
  font-size: 14px;
  margin-left: -19px;
  top: 1px;
  position: relative;
  margin-right: 8px;
  font-weight: bolder; }

.radio input[type='radio']:checked + label::before,
.radio input[type='radio']:checked + span::before,
.radio-inline input[type='radio']:checked + label::before,
.radio-inline input[type='radio']:checked + span::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-left: -18px;
  margin-right: 8px;
  background: #c94281; }

.radio input[type='radio'] + label,
.radio input[type='radio'] + span,
.radio-inline input[type='radio'] + label,
.radio-inline input[type='radio'] + span {
  padding-left: 23px;
  margin-left: -23px; }

.checkbox label,
.radio label {
  padding-left: 0px; }

